import axios from 'axios'

export default {
    namespaced: true,

    state: {
        records: [],
        loadingRecords: false,
        categories: [],
        departments: [],
        record: {},
    },

    getters: {},

    mutations: {
        setRecords: (state, payload) => (state.records = payload),
        setLoadingRecords: (state, payload) => (state.loadingRecords = payload),
        setCategories: (state, payload) => (state.categories = payload),
        setDepartments: (state, payload) => (state.departments = payload),
        setRecord: (state, payload) => (state.record = payload),
        setDeletedRecord(state, payload) {
            state.records = state.records.filter(item => {
                return item.id !== payload
            })
        }
    },

    actions: {
        async getRecords({ commit }) {
            commit("setLoadingRecords", true)
            try {
                const response = await axios.get('v1/tickets/my-tickets');

                commit('setRecords', response.data);
            } catch (error) {
                alert('The system encountered an error. Please refresh the page.')
            } finally {
                commit("setLoadingRecords", false)
            }
        },

        async createRecord({ commit }) {
            commit("setLoadingRecords", true)
            try {
                const response = await axios.get('v1/tickets/my-tickets/create');

                commit('setCategories', response.data.categories);
                commit('setDepartments', response.data.departments);
            } catch (error) {
                alert('The system encountered an error. Please refresh the page.')
            } finally {
                commit("setLoadingRecords", false)
            }
        },

        async storeRecord({ commit }, data) {
            commit("setLoadingRecords", true)

            try {
                const response = await axios.post('v1/tickets/my-tickets', data);

                commit('setNotification', {
                    type: response.data.type,
                    message: response.data.message
                }, { root: true })
            } catch (error) {
                alert('The system encountered an error. Please refresh the page.');
            } finally {
                commit("setLoadingRecords", false)
            }
        },

        async editRecord({ commit }, id) {
            commit("setLoadingRecords", true)
            try {
                const response = await axios.get(`v1/tickets/my-tickets/${id}/edit`);

                commit('setRecord', response.data.ticket);
                commit('setCategories', response.data.categories);
                commit('setDepartments', response.data.departments);
            } catch (error) {
                alert('The system encountered an error. Please refresh the page.')
            } finally {
                commit("setLoadingRecords", false)
            }
        },

        async updateRecord({ commit }, [id, data]) {
            commit("setLoadingRecords", true)

            try {
                const response = await axios.put(`v1/tickets/my-tickets/${id}`, data);

                commit('setNotification', {
                    type: response.data.type,
                    message: response.data.message
                }, { root: true })
            } catch (error) {
                alert('The system encountered an error. Please refresh the page.');
            } finally {
                commit("setLoadingRecords", false)
            }
        },

        async deleteRecord({ commit }, id) {
            commit("setLoadingRecords", true)
            try {
                const response = await axios.delete(`v1/tickets/my-tickets/${id}`);

                commit('setDeletedRecord', id);
                commit('setNotification', {
                    type: response.data.type,
                    message: response.data.message
                }, { root: true })
            } catch (error) {
                alert('The system encountered an error. Please refresh the page.')
            } finally {
                commit("setLoadingRecords", false)
            }
        },
    }
}
