  <div>
    <div v-if="!authenticated">
      <v-app-bar elevation="4" class="px-md-16" app>
        <v-img
          class="mr-2"
          :src="require('@/assets/nsjbi_logo.png')"
          max-height="40"
          max-width="40"
          contain
        ></v-img>
        <v-toolbar-title v-show="$vuetify.breakpoint.mdAndUp ? true : false"
          >Helpline</v-toolbar-title
        >

        <v-spacer></v-spacer>

        <v-btn to="/" text class="text-capitalize mr-1" v-text="'Home'"></v-btn>
        <v-btn
          to="/knowledge-base"
          text
          class="text-capitalize mr-1"
          v-text="'Knowledge Base'"
