        <v-btn
          to="/login"
          text
          class="text-capitalize"
          v-text="'Login'"
        ></v-btn>
      </v-app-bar>
    </div>
    <div v-else>
      <v-navigation-drawer v-model="drawer" app>
        <v-list-item>
          <v-list-item-avatar>
            <v-img :src="require('@/assets/nsjbi_logo.png')"> </v-img>
          </v-list-item-avatar>

          <v-list-item-content>
            <v-list-item-title class="text-h6"> Helpline </v-list-item-title>
            <v-list-item-subtitle class="ml-1"> NSJBI </v-list-item-subtitle>
          </v-list-item-content>
        </v-list-item>

        <v-list dense nav>
          <v-list-item link router :to="{ name: 'Dashboard' }" color="primary">
            <v-list-item-icon>
              <v-icon>mdi-view-dashboard</v-icon>
            </v-list-item-icon>

            <v-list-item-content>
              <v-list-item-title>Dashboard</v-list-item-title>
