          </v-list-item>

          <v-list-item
            link
            router
            :to="{ name: 'Accounts' }"
            color="primary"
            v-if="user.role_id == 1"
          >
            <v-list-item-icon>
              <v-icon>mdi-account-group</v-icon>
            </v-list-item-icon>

            <v-list-item-content>
              <v-list-item-title>Accounts</v-list-item-title>
            </v-list-item-content>
          </v-list-item>

          <v-subheader class="text-uppercase">Tickets</v-subheader>

          <v-list-item link router :to="{ name: 'MyTeam' }" color="primary">
            <v-list-item-icon>
              <v-icon>mdi-briefcase</v-icon>
            </v-list-item-icon>

            <v-list-item-content>
              <v-list-item-title>My Team</v-list-item-title>
            </v-list-item-content>
          </v-list-item>

          <v-list-item link router :to="{ name: 'MyQueue' }" color="primary">
            <v-list-item-icon>
              <v-icon>mdi-human-queue</v-icon>
            </v-list-item-icon>

            <v-list-item-content>
              <v-list-item-title>My Queue</v-list-item-title>
            </v-list-item-content>
          </v-list-item>

          <v-list-item link router :to="{ name: 'MyTicket' }" color="primary">
            <v-list-item-icon>
