            </v-list-item-icon>

            <v-list-item-content>
              <v-list-item-title>My Tickets</v-list-item-title>
            </v-list-item-content>
          </v-list-item>

          <v-list-item link router :to="{ name: 'Solutions' }" color="primary">
            <v-list-item-icon>
              <v-icon>mdi-book-open-variant</v-icon>
            </v-list-item-icon>

            <v-list-item-content>
              <v-list-item-title>Solutions</v-list-item-title>
            </v-list-item-content>
          </v-list-item>

          <!-- <v-subheader class="text-uppercase">Reports</v-subheader>

          <v-list-item link router color="primary">
            <v-list-item-icon>
              <v-icon>mdi-file</v-icon>
            </v-list-item-icon>

            <v-list-item-content>
              <v-list-item-title>First Report</v-list-item-title>
            </v-list-item-content>
          </v-list-item>

          <v-list-item link router color="primary">
            <v-list-item-icon>
              <v-icon>mdi-file</v-icon>
            </v-list-item-icon>

            <v-list-item-content>
              <v-list-item-title>Second Report</v-list-item-title>
            </v-list-item-content>
          </v-list-item> -->

          <v-subheader class="text-uppercase">Settings</v-subheader>

          <v-list-item link router :to="{ name: 'Profile' }" color="primary">
            <v-list-item-icon>
              <v-icon>mdi-account-cog</v-icon>
            </v-list-item-icon>

            <v-list-item-content>
              <v-list-item-title>Profile</v-list-item-title>
            </v-list-item-content>
          </v-list-item>

          <v-list-item link router color="primary" @click="logout">
            <v-list-item-icon>
              <v-icon>mdi-logout</v-icon>
            </v-list-item-icon>

            <v-list-item-content>
              <v-list-item-title>Logout</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </v-list>
      </v-navigation-drawer>

      <v-app-bar elevate-on-scroll app>
        <v-app-bar-nav-icon @click="drawer = !drawer"></v-app-bar-nav-icon>
        <v-toolbar-title>{{ $route.meta.title }}</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-btn icon>
          <v-icon>mdi-bell</v-icon>
        </v-btn>
      </v-app-bar>
    </div>

    <v-dialog v-model="logoutDialog" persistent width="300">
      <v-card class="pt-2">
        <v-card-text>
          Logging out
          <v-progress-linear
            indeterminate
            color="primary"
            class="mb-0"
          ></v-progress-linear>
        </v-card-text>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  data: () => ({
    drawer: null,
    logoutDialog: false,
  }),

  computed: {
    ...mapGetters("auth", ["authenticated", "user"]),
  },

  methods: {
    async logout() {
      this.logoutDialog = true
      await this.$store.dispatch("auth/logout");
      this.$router.push({ name: "Home" });
      this.logoutDialog = false
    },
  },
};
</script>