import axios from 'axios'
import router from '@/router';

export default {
    namespaced: true,

    state: {
        token: null,
        user: null,
        loading: false,
        errors: [],
    },

    getters: {
        authenticated(state) {
            return state.token && state.user
        },
        isLoggedIn(state) {
            return state.token && state.loading == false
        },
        user: (state) => (state.user),
        loading: (state) => (state.loading),
        errors: (state) => (state.errors),
    },

    mutations: {
        setToken: (state, data) => (state.token = data),
        setUser: (state, data) => (state.user = data),
        setLoading: (state, data) => (state.loading = data),
        setErrors: (state, payload) => (state.errors = payload),
    },

    actions: {
        async login({ dispatch, commit }, data) {
            commit("setLoading", true)

            try {
                const response = await axios.post('v1/login', data);

                if (response.data.type == 'error') {
                    commit('setNotification', {
                        type: response.data.type,
                        message: response.data.message
                    }, { root: true })
                } else {
                    return dispatch('attempt', response.data.token)
                }
            } catch (error) {
                alert('The system encountered an error. Please refresh the page.');
            } finally {
                commit("setLoading", false)
            }
        },

        async attempt({ commit, state }, data) {
            if (data) {
                commit('setToken', data)
            }

            if (!state.token) {
                return
            }

            try {
                const response = await axios.get('v1/user')

                commit('setUser', response.data)
            } catch (e) {
                commit('setToken', null)
                commit('setUser', null)
            }
        },

        async changePassword({ commit }, data) {
            commit("setLoading", true)
            commit("setErrors", [])

            try {
                const response = await axios.patch('v1/change-password', data);

                commit('setNotification', {
                    type: response.data.type,
                    message: response.data.message
                }, { root: true })
            } catch (error) {
                if (error.response.status === 422) {
                    commit("setErrors", error.response.data.errors)
                } else {
                    alert('The system encountered an error. Please refresh the page.');
                }
            } finally {
                commit("setLoading", false)
            }
        },

        logout({ commit }) {
            return axios.post('v1/logout').then(() => {
                commit('setToken', null)
                commit('setUser', null)
            })
        },

        async redirectLogin({ dispatch }, data) {
            try {
                await axios.get('v1/validate-token', {
                    headers: {
                        Authorization: 'Bearer ' + data
                    }
                });

                return dispatch('attempt', data)
            } catch (error) {
                alert('The system encountered an error. Please refresh the page.');
                router.replace({ name: "Home" });
            }
        },
    }
}
