<template>
  <v-dialog v-model="dialog" persistent width="300">
    <v-card class="pt-2">
      <v-card-text>
        Logging in
        <v-progress-linear
          indeterminate
          color="primary"
          class="mb-0"
        ></v-progress-linear>
      </v-card-text>
    </v-card>
  </v-dialog>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  name: "RedirectLogin",

  data: () => ({
    dialog: true,
  }),

  created() {
    this.login();
  },

  computed: {
    ...mapGetters("auth", ["authenticated"]),
  },

  methods: {
    async login() {
      await this.$store.dispatch("auth/redirectLogin", this.$route.params.token);
      this.dialog = false;
    },
  },

  watch: {
    authenticated(val) {
      if (val) {
        this.$router.replace({ name: "CreateTicket" });
      }
    },
  },
};
</script>