import axios from 'axios'

export default {
    namespaced: true,

    state: {
        records: [],
        loadingRecords: false,
        departments: [],
        roles: [],
        errors: [],
        record: {},
    },

    getters: {},

    mutations: {
        setRecords: (state, payload) => (state.records = payload),
        setLoadingRecords: (state, payload) => (state.loadingRecords = payload),
        setDepartments: (state, payload) => (state.departments = payload),
        setRoles: (state, payload) => (state.roles = payload),
        setErrors: (state, payload) => (state.errors = payload),
        setRecord: (state, payload) => (state.record = payload),
        setDeletedRecord(state, payload) {
            state.records = state.records.filter(item => {
                return item.id !== payload
            })
        }
    },

    actions: {
        async getRecords({ commit }) {
            commit("setLoadingRecords", true)
            try {
                const response = await axios.get('v1/users');

                commit('setRecords', response.data);
            } catch (error) {
                alert('The system encountered an error. Please refresh the page.')
            } finally {
                commit("setLoadingRecords", false)
            }
        },

        async createRecord({ commit }) {
            commit("setLoadingRecords", true)
            try {
                const response = await axios.get('v1/users/create');

                commit('setDepartments', response.data.departments);
                commit('setRoles', response.data.roles);
            } catch (error) {
                alert('The system encountered an error. Please refresh the page.')
            } finally {
                commit("setLoadingRecords", false)
            }
        },

        async storeRecord({ commit }, data) {
            commit("setLoadingRecords", true)
            commit("setErrors", [])

            try {
                const response = await axios.post('v1/users', data);

                commit('setNotification', {
                    type: response.data.type,
                    message: response.data.message
                }, { root: true })
            } catch (error) {
                if (error.response.status === 422) {
                    commit("setErrors", error.response.data.errors)
                } else {
                    alert('The system encountered an error. Please refresh the page.');
                }
            } finally {
                commit("setLoadingRecords", false)
            }
        },

        async editRecord({ commit }, data) {
            commit("setLoadingRecords", true)
            try {
                const response = await axios.get(`v1/users/${data}/edit`);

                commit('setRecord', response.data.user);
                commit('setDepartments', response.data.departments);
                commit('setRoles', response.data.roles);
            } catch (error) {
                alert('The system encountered an error. Please refresh the page.')
            } finally {
                commit("setLoadingRecords", false)
            }
        },

        async updateRecord({ commit }, [id, data]) {
            commit("setLoadingRecords", true)
            commit("setErrors", [])

            try {
                const response = await axios.put(`v1/users/${id}`, data);

                commit('setNotification', {
                    type: response.data.type,
                    message: response.data.message
                }, { root: true })
            } catch (error) {
                if (error.response.status === 422) {
                    commit("setErrors", error.response.data.errors)
                } else {
                    alert('The system encountered an error. Please refresh the page.');
                }
            } finally {
                commit("setLoadingRecords", false)
            }
        },

        async deleteRecord({ commit }, id) {
            commit("setLoadingRecords", true)
            try {
                const response = await axios.delete(`v1/users/${id}`);

                commit('setDeletedRecord', id);
                commit('setNotification', {
                    type: response.data.type,
                    message: response.data.message
                }, { root: true })
            } catch (error) {
                alert('The system encountered an error. Please refresh the page.')
            } finally {
                commit("setLoadingRecords", false)
            }
        },
    }
}
