import axios from 'axios'
import router from '@/router';

export default {
    namespaced: true,

    state: {
        records: [],
        loadingRecords: false,
        loadingForm: false,
        record: {},
        categories: [],
        ticketStatuses: [],
        priorityStatuses: [],
        departments: [],
        agents: [],
        errors: [],

    },

    getters: {},

    mutations: {
        setRecords: (state, payload) => (state.records = payload),
        setLoadingRecords: (state, payload) => (state.loadingRecords = payload),
        setLoadingForm: (state, payload) => (state.loadingForm = payload),
        setRecord: (state, payload) => (state.record = payload),
        setCategories: (state, payload) => (state.categories = payload),
        setTicketStatuses: (state, payload) => (state.ticketStatuses = payload),
        setPriorityStatuses: (state, payload) => (state.priorityStatuses = payload),
        setDepartments: (state, payload) => (state.departments = payload),
        setAgents: (state, payload) => (state.agents = payload),
        setErrors: (state, payload) => (state.errors = payload),
    },

    actions: {
        async getRecords({ commit }) {
            commit("setLoadingRecords", true)

            try {
                const response = await axios.get('v1/tickets/my-team');

                commit('setRecords', response.data);
            } catch (error) {
                alert('The system encountered an error. Please refresh the page.')
            } finally {
                commit("setLoadingRecords", false)
            }
        },

        async editRecord({ commit }, id) {
            commit("setLoadingRecords", true)
            commit("setLoadingForm", true)

            try {
                const response = await axios.get(`v1/tickets/my-team/${id}/edit`);

                commit('setRecord', response.data.ticket);
                commit('setCategories', response.data.categories);
                commit('setTicketStatuses', response.data.ticketStatuses);
                commit('setPriorityStatuses', response.data.priorityStatuses);
                commit('setDepartments', response.data.departments);
                commit('setAgents', response.data.agents);
            } catch (error) {
                router.push({ name: "Dashboard" });
                commit('setNotification', {
                    type: 'error',
                    message: 'Page not found'
                }, { root: true })
            } finally {
                commit("setLoadingRecords", false)
                commit("setLoadingForm", false)
            }
        },

        async getAgents({ commit }, id) {
            commit("setLoadingForm", true)

            try {
                const response = await axios.get(`v1/agents/${id}`);

                commit('setAgents', response.data);
            } catch (error) {
                alert('The system encountered an error. Please refresh the page.')
            } finally {
                commit("setLoadingForm", false)
            }
        },

        async updateRecord({ commit }, [id, data]) {
            commit("setLoadingForm", true)
            commit("setErrors", [])

            try {
                const response = await axios.put(`v1/tickets/my-team/${id}`, data);

                commit('setNotification', {
                    type: response.data.type,
                    message: response.data.message
                }, { root: true })
            } catch (error) {
                if (error.response.status === 422) {
                    commit("setErrors", error.response.data.errors)
                } else {
                    alert('The system encountered an error. Please refresh the page.');
                }
            } finally {
                commit("setLoadingForm", false)
            }
        },
    }
}
