import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import knowledgeBase from './modules/knowledge-base'
import auth from './modules/auth'
import dashboard from './modules/dashboard'
import account from './modules/account'
import myTeam from './modules/ticket/my-team'
import myQueue from './modules/ticket/my-queue'
import myTicket from './modules/ticket/my-ticket'
import solution from './modules/ticket/solution'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    notifications: [],
    ticketLogs: [],
    solutions: [],
    loading: false
  },

  getters: {
  },

  mutations: {
    setNotification(state, data) {
      state.notifications.push({
        ...data,
        id: (Math.random().toString(36) + Date.now().toString(36)).substr(2)
      })
    },

    setRemoveNotification(state, data) {
      state.notifications = state.notifications.filter(notification => {
        return notification.id != data.id
      })
    },

    setTicketLogs: (state, payload) => (state.ticketLogs = payload),
    setSolutions: (state, payload) => (state.solutions = payload),
    setLoading: (state, payload) => (state.loading = payload),
    newSolution: (state, payload) => state.solutions.unshift(payload),
  },

  actions: {
    addNotification({ commit }, data) {
      commit('setNotification', data)
    },

    removeNotification({ commit }, data) {
      commit('setRemoveNotification', data)
    },

    async getSolutions({ commit }, id) {
      commit("setLoading", true)

      try {
        const response = await axios.get(`v1/solutions/${id}`);

        commit('setSolutions', response.data);
      } catch (error) {
        alert('The system encountered an error.')
      } finally {
        commit("setLoading", false)
      }
    },

    async getTicketLogs({ commit }, id) {
      commit("setLoading", true)

      try {
        const response = await axios.get(`v1/ticket-logs/${id}`);

        commit('setTicketLogs', response.data);
      } catch (error) {
        alert('The system encountered an error.')
      } finally {
        commit("setLoading", false)
      }
    }
  },

  modules: {
    knowledgeBase,
    auth,
    dashboard,
    account,
    myTeam,
    myQueue,
    myTicket,
    solution
  }
})
