import axios from 'axios'

export default {
    namespaced: true,

    state: {
        records: {},
        loadingRecords: false,
        tickets: [],
        loadingDialog: false,
    },

    getters: {},

    mutations: {
        setRecords: (state, payload) => (state.records = payload),
        setLoadingRecords: (state, payload) => (state.loadingRecords = payload),
        setTickets: (state, payload) => (state.tickets = payload),
        setLoadingDialog: (state, payload) => (state.loadingDialog = payload),
    },

    actions: {
        async getRecords({ commit }) {
            commit("setLoadingRecords", true)

            try {
                const response = await axios.get('v1/dashboard');

                commit('setRecords', response.data.records);
            } catch (error) {
                alert("The system encountered an error.")
            } finally {
                commit("setLoadingRecords", false)
            }
        },

        async getTickets({ commit }, id) {
            commit("setLoadingDialog", true)

            try {
                const response = await axios.get(`v1/dashboard/tickets/${id}`);

                commit('setTickets', response.data);
            } catch (error) {
                alert("The system encountered an error.")
            } finally {
                commit("setLoadingDialog", false)
            }
        },
    }
}
