import Vue from 'vue'
import VueRouter from 'vue-router'
import store from '@/store'
import HomeView from '../views/HomeView.vue'
import RedirectLogin from '../views/auth/RedirectLogin.vue'

Vue.use(VueRouter)

const guest = (to, from, next) => {
  if (!store.getters['auth/authenticated']) {
    return next()
  } else {
    return next({ name: 'Dashboard' })
  }
}

const auth = (to, from, next) => {
  if (store.getters['auth/authenticated']) {
    return next()
  } else {
    return next({ name: 'Home' })
  }
}

const isAdmin = (to, from, next) => {
  if (store.getters['auth/authenticated']) {
    if (store.getters['auth/user'].role_id == 1) {
      return next()
    } else {
      return next({ name: 'Dashboard' })
    }
  } else {
    return next({ name: 'Home' })
  }
}

const routes = [
  {
    path: '/',
    name: 'Home',
    component: HomeView,
    beforeEnter: guest
  },
  {
    path: '/redirect-login/:token',
    name: 'RedirectLogin',
    component: RedirectLogin,
    beforeEnter: guest
  },
  {
    path: '/knowledge-base',
    name: 'KnowledgeBase',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/KnowledgeBase.vue'),
    beforeEnter: guest
  },
  {
    path: '/login',
    name: 'Login',
    // route level code-splitting
    // this generates a separate chunk (authLogin.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "authLogin" */ '../views/auth/Login.vue'),
    beforeEnter: guest
  },
  {
    path: '/dashboard',
    name: 'Dashboard',
    // route level code-splitting
    // this generates a separate chunk (dashboard.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "dashboard" */ '../views/Dashboard.vue'),
    beforeEnter: auth,
    meta: {
      title: 'Dashboard'
    }
  },
  {
    path: '/ticket/solutions',
    name: 'Solutions',
    // route level code-splitting
    // this generates a separate chunk (ticketSolutionShowAll.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "ticketSolutionShowAll" */ '../views/ticket/solution/ShowAll.vue'),
    beforeEnter: auth,
    meta: {
      title: 'Solutions'
    }
  },
  {
    path: '/accounts',
    name: 'Accounts',
    // route level code-splitting
    // this generates a separate chunk (accountShowAll.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "accountShowAll" */ '../views/account/ShowAll.vue'),
    beforeEnter: isAdmin,
    meta: {
      title: 'Accounts'
    }
  },
  {
    path: '/accounts/create',
    name: 'CreateAccount',
    // route level code-splitting
    // this generates a separate chunk (accountCreate.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "accountCreate" */ '../views/account/Create.vue'),
    beforeEnter: isAdmin,
    meta: {
      title: 'Accounts'
    }
  },
  {
    path: '/accounts/edit/:id',
    name: 'EditAccount',
    // route level code-splitting
    // this generates a separate chunk (accountEdit.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "accountEdit" */ '../views/account/Edit.vue'),
    beforeEnter: isAdmin,
    meta: {
      title: 'Accounts'
    }
  },
  {
    path: '/tickets/my-team',
    name: 'MyTeam',
    // route level code-splitting
    // this generates a separate chunk (ticketMyTeamShowAll.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "ticketMyTeamShowAll" */ '../views/ticket/my-team/ShowAll.vue'),
    beforeEnter: auth,
    meta: {
      title: 'My Team'
    }
  },
  {
    path: '/tickets/my-team/edit/:id',
    name: 'MyTeamEdit',
    // route level code-splitting
    // this generates a separate chunk (ticketMyTeamEdit.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "ticketMyTeamEdit" */ '../views/ticket/my-team/Edit.vue'),
    beforeEnter: auth,
    meta: {
      title: 'My Team'
    }
  },
  {
    path: '/tickets/my-queue',
    name: 'MyQueue',
    // route level code-splitting
    // this generates a separate chunk (ticketMyQueueShowAll.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "ticketMyQueueShowAll" */ '../views/ticket/my-queue/ShowAll.vue'),
    beforeEnter: auth,
    meta: {
      title: 'My Queue'
    }
  },
  {
    path: '/tickets/my-queue/edit/:id',
    name: 'MyQueueEdit',
    // route level code-splitting
    // this generates a separate chunk (ticketMyQueueEdit.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "ticketMyQueueEdit" */ '../views/ticket/my-queue/Edit.vue'),
    beforeEnter: auth,
    meta: {
      title: 'My Queue'
    }
  },
  {
    path: '/tickets/my-tickets',
    name: 'MyTicket',
    // route level code-splitting
    // this generates a separate chunk (ticketMyTicketShowAll.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "ticketMyTicketShowAll" */ '../views/ticket/my-ticket/ShowAll.vue'),
    beforeEnter: auth,
    meta: {
      title: 'My Tickets'
    }
  },
  {
    path: '/tickets/my-tickets/create',
    name: 'CreateTicket',
    // route level code-splitting
    // this generates a separate chunk (ticketMyTicketCreate.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "ticketMyTicketCreate" */ '../views/ticket/my-ticket/Create.vue'),
    beforeEnter: auth,
    meta: {
      title: 'My Tickets'
    }
  },
  {
    path: '/tickets/my-tickets/edit/:id',
    name: 'EditTicket',
    // route level code-splitting
    // this generates a separate chunk (ticketMyTicketEdit.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "ticketMyTicketEdit" */ '../views/ticket/my-ticket/Edit.vue'),
    beforeEnter: auth,
    meta: {
      title: 'My Tickets'
    }
  },
  {
    path: '/profile',
    name: 'Profile',
    // route level code-splitting
    // this generates a separate chunk (authShow.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "authShow" */ '../views/auth/Show.vue'),
    beforeEnter: auth,
    meta: {
      title: 'Profile'
    }
  },
  {
    path: '/:catchAll(.*)',
    name: 'NotFound',
    component: () => import(/* webpackChunkName: "pagenotfound" */ '../views/NotFound.vue'),
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
